* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
}
body {
  width: 100%;
  height: 100%;
  background-color: #000;
  font-family: sans-serif;
}
a {
  text-decoration: none;
  color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6,
span {
  font-size: 18px;
  color: #fff;
  font-family: Poppins, sans-serif;
}
li {
  overflow: hidden;
}
span {
  display: block;
}
p {
  font-size: 16px;
  font-family: Poppins, sans-serif;
}
.white {
  color: #fff !important;
}
.black {
  color: #000 !important;
}
.d_flex {
  display: flex;
}
.align_items_center {
  align-items: center;
}
.justify_content_center {
  justify-content: center;
}
.justify_content_space_between {
  justify-content: space-between;
}
.mx_15 {
  margin: 0px 15px;
}
.px_30 {
  padding: 0px 30px;
}
.pxy_30 {
  padding: 30px;
}
.container {
  width: 92%;
  margin: 0px auto;
}
.pz_10 {
  padding: 10px 0px;
}

.row {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  overflow: hidden;
  align-items: flex-start;
  justify-content: center;
}
.col_2 {
  max-width: 50%;
  display: flex;
  padding: 20px;
  justify-content: center;
  flex-direction: column;
}
.col_3 {
  max-width: 33.333333%;
  display: flex;
  padding: 20px;
  justify-content: center;
  flex-direction: column;
  margin-top: 20px;
}
.component_space {
  padding: 100px 0px;
}
.pointer {
  cursor: pointer;
}
svg {
  stroke-width: 1 !important;
}
.relative {
  position: relative;
}
.absolute {
  position: absolute;
}
strong {
  color: #f8675d;
  font-weight: 600;
}
input {
  color: rgba(198, 201, 216, 0.75);
}
button.btn {
  font-size: 18px;
  font-weight: 500;
  border-radius: 5px;
  border: 2px solid #f8675d;
  background-color: #f8675d;
  color: #fff !important;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: 0.4s all ease;
  height: 50px;
  width: 180px;
  margin: 0px 10px;
}
.p_color {
  color: rgba(198, 201, 216, 0.75);
  font-size: 18px;
  line-height: 30px;
  font-family: Poppins, sans-serif;
  margin-bottom: 15px;
}
button.about.btn.pointer:hover {
  background: #fff;
  color: #000 !important;
}
/* RESPONSIVE */
@media only screen and (max-width: 768px) {
  .col_2 {
    max-width: 100%;
  }
}
@media only screen and (max-width: 768px) {
  .col_3 {
    max-width: 50%;
  }
}
@media only screen and (max-width: 425px) {
  .col_3 {
    max-width: 100%;
  }
}
@media only screen and (max-width: 425px) {
  span.writing {
    font-size: 13px !important;
  }
  .footer.d_flex.align_items_center.justify_content_space_between.pz-10 {
    flex-wrap: wrap;
  }
}
